* {
  box-sizing: border-box;
}

.page {
  min-height: 100vh;
}
.page .content-aria {
  width: calc(100% - 250px);
  min-height: 100%;
  transition: width 0.3s;
  float: right;
}
.page .content-aria.no-sidebar {
  width: 100%;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #eee;
  padding: 1rem;
  transition: left 0.3s;
}
.page .sidebar.no-sidebar {
  left: -250px;
}

.header .toggle-sidebar {
  cursor: pointer;
}